      </div>
      <div class="col-lg-6 col-md-6 col-sm-12 col-12 export">
        <v-select v-model="select" :items="items" item-text="label" item-value="value" outlined></v-select>
        <button class="btn btn-ch">
          Exportar
        </button>
      </div>
