    <div class="row">
      <div class="col-12">
        <table class="table">
          <tr v-for="(r, key) in restaurants" :key="key">
            <td class="t-img">
              <v-img lazy-src="@/assets/Images/Restaurant.png"  :src="getImage(r.get('image'))" alt="restaurant-logo" width="80" height="80"></v-img>
            </td>
            <td class="t-name">
              <h3>{{ r.get('name') }}</h3>
              <p>{{ r.get('schedule') }}</p>
            </td>
            <td :class="color(r.get('open'))" v-html="state(r.get('open'))" class="t-status"></td>
            <td class="t-price">${{numberFormat(r.get('sales'))}}</td>
            <td class="t-switch">
              <v-switch color="#26E822" :input-value="r.get('open')"  @change="changeState(key, r.get('open'))" hide-details>
              </v-switch>
            </td>
            <td>
              <button class="t-button" @click="goDetail(r.get('objectId'))">Ver detalles</button>
            </td>
          </tr>
        </table>
      </div>
    </div>

    <v-row class="text-left" id="buttons">
      <v-col cols="12" sm="6">
        <div class="text-left">
          <div class="my-3">
            <v-btn @click="toNotifications" class="bell-button" icon color="black" fab large dark top left>
              <v-icon>mdi-bell-outline </v-icon>
            </v-btn>
          </div>

          <template>
            <div class="t-dialog">



              <v-dialog v-model="dialog" persistent max-width="650" class="position-relative d-add-restaurant">


                <!-- START CONTENT DIALOG ADD RESTAURANT -->
                <template v-slot:activator="{ on, attrs }">

                  <v-btn color="red" fab large dark top left v-bind="attrs" v-on="on">
                    <v-icon>mdi-plus</v-icon>
                  </v-btn>
                </template>
                <v-card>

                  <v-btn class="mx-2" fab light small color="#E6E6E6" style="float:right; top: 5px; right:5px"
                    @click="close">
                    <v-icon dark>
                      mdi-close
                    </v-icon>
                  </v-btn>

                  <div class="p-5 ">

                    <v-form class="needs-validation" ref="form" v-model="valid" @submit="checkForm" lazy-validation>
                      <div class="form mt-5">

                        <div class="row">

                          <div class="d-flex d-flex-row align-items-center col-12">
                            <div class="position-relative t-image" style="width:90px; height:90px">
                              <v-img v-bind:src="RestaurantImage"
                              rounded
                                ref="img"
                                v-if="RestaurantImage.length > 0"
                                lazy-src="@/assets/Images/Vector.png"
                                alt="restaurant-logo" width="90" height="90">
                              </v-img>
                              <img class="img-fluid" v-if="RestaurantImage.length <= 0" src="@/assets/Images/Vector.png" alt="logo" width="90"/>
                              <v-file-input prepend-icon="mdi-camera" class="position-absolute"
                                style="top:22px;left:33px;" hide-input @change="Preview_image"
                                ref="fileupload"  accept="image/*"
                                v-model="image"></v-file-input>

                            </div>
                            <div>
                              <v-text-field class="edt-title-popup" :rules="nameRules" v-model="name" type="text"
                                placeholder="Nombre del restaurante" required></v-text-field>
                              <div class="invalid-feedback text-left m-0" style="margin-left: 2rem !important;">
                                Campo requerido
                              </div>
                            </div>
                          </div>


                          <div class="col-12">
                            <p class="txt-label text-left">Nombre del representante legal</p>
                            <v-text-field class="edt-popup" :rules="representativeRules" v-model="representative"
                              type="text" required>
                            </v-text-field>
                            <div class="invalid-feedback text-left m-0">
                              Campo requerido
                            </div>
                          </div>

