<script>
  import Parse from "parse"
  export default {
    data() {
      return {
        valid: false,
        dialog: false,
        loading: false,
        saving: false,
        RestaurantImage: '',
        image: null,
        option: "1",
        select: {
          label: "Ventas último mes",
          value: "1",
        },
        items: [{
            label: "Ventas último mes",
            value: "1",
          },
          {
            label: "Ventas última semana",
            value: "2",
          },
        ],
        restaurants: [],
        selectedFile : null,
        name: null,
        nameRules: [
          v => !!v || 'Nombre es requerido',
          v => (v && v.length <= 156) || 'Debe tener menos de 156 caracteres'
        ],
        representative: null,
        representativeRules: [
          v => !!v || 'Representante es requerido',
          v => (v && v.length <= 156) || 'Debe tener menos de 156 caracteres'
        ],
        document: null,
        documentRules: [
          v => !!v || 'Identificación es requerida'
        ],
        phone: null,
        phoneRules: [
          v => !!v || 'Teléfono es requerido'
        ],
        email: null,
        emailRules: [
          v => !!v || 'E-mail es requerido',
          v => /.+@.+/.test(v) || 'E-mail debe ser valido'
        ],
        menuUrl: null,
        payment: 'Efectivo',
        user: null,
        userRules: [
          v => !!v || 'Usuario es requerido'
        ],
        pass: null,
        passRules: [
          v => !!v || 'Clave es requerida',
          v => (v && v.length >= 6) || 'Debe tener al menos 6 caracteres'
        ],
        /*restaurants: [{
          id: "SD65AS74",
            name: "Restaurante 1",
            status: false,
            price: "$20.000.000",
          }
        ],*/
      };
    },
    computed: {

    },
    created() {
      this.$store.state.title = "Restaurantes";
      this.fetchRestaurants();


    },
    methods: {
      getImage(file) {
        try{
          return file.url()
        }catch(error){
          return ''
        }
        
      },
      Preview_image(event) {
        if(this.image!=null)
        this.RestaurantImage = URL.createObjectURL(this.image)
        this.selectedFile = event
      },
      checkForm(event) {
        event.preventDefault();
        event.target.classList.add('was-validated');
      },
      submit() {
        if (this.$refs.form.validate()) {
          this.createUserRestaurant()
        }
      },
      close() {
        this.clear();
        this.dialog = false;
      },
      async createUserRestaurant() {
        let sessionToken = Parse.User.current().getSessionToken(),
          user = new Parse.User()
        user.set("username", this.user)
        user.set("password", this.pass)
        user.set("email", this.email)
        user.set("rol", 'kitchen')
        this.saving = true;
        try {
          await user.signUp();
          Parse.User.become(sessionToken)
          this.saveRestaurant(user)
        } catch (error) {
          this.$eventBus.$emit('message', error.message)
        } finally {
          this.saving = false;
        }
      },
      async saveRestaurant(user) {
        var Restaurant = Parse.Object.extend("Restaurant")
        var restaurant = new Restaurant()
        try {
          await restaurant.save({
            name: this.name,
            representative: this.representative,
            document: this.document,
            phone: this.phone,
            email: this.email,
            menuUrl: this.menuUrl,
            paymentMethod: this.payment,
            schedule: '6:00am - 8:00pm',
            sales: 19500000,
            image: new Parse.File("profile.png", this.selectedFile),
            user: user
          })
          this.$eventBus.$emit('message', 'Restaurante agregado!')
          this.clear()
          this.dialog = false
          this.fetchRestaurants()
        } catch (error) {
          this.$eventBus.$emit('message', error.message)
        }
      },
      clear() {
        this.$refs.form.reset()
        this.RestaurantImage = ''
        this.selectedFile = null
      },
      getStatus(key) {
        console.log(key)
        return false;
      },
      async changeState(key, isActive) {
        const o = this.restaurants[key];
        o.set("open", !isActive);
        try {
          await o.save();
          this.restaurants.splice(key, 1);
          this.restaurants.splice(key, 0, o);
        } catch (error) {
          console.log(error);
          console.log("FAIL- changeState");
        } finally {
          console.log("done")
        }

      },
      async fetchRestaurants() {
        this.loading = true;
        const Restaurants = Parse.Object.extend("Restaurant");
        const query = new Parse.Query(Restaurants);
        this.restaurants = await query.find();
        this.loading = false;
      },
      state(status) {
        if (status) {
          return "Abierto";
        } else {
          return "Cerrado";
        }
      },
      color(status) {
        if (status) {
          return "r-open";
        } else {
          return "r-close";
        }
      },
      toNotifications() {
        this.$router.push('/notificaciones')
      },
      goDetail(idRestaurant) {
        this.$router.push({
          name: 'Restaurant',
          params: {
            id: idRestaurant
          }
        });
      },
      numberFormat(n) {
        return Number(n).toLocaleString('en');
      }
    },
  };
</script>

